<template>
  <div class="order">
    <!-- 顶部 tab 栏 -->
    <van-tabs
      v-model="activeName"
      color="slateblue"
      animated
      swipeable
      title-active-color="slateblue"
      title-inactive-color="#999"
      line-width="10"
      sticky
      @change="getOrderData"
    >
      <van-tab :title="item.title" :name="item.name" v-for="(item , index) in tabData" :key="index">
        <div
          class="order-content"
          v-show="orderNum.lenght != 0"
          v-for="(v , i) in orderNum"
          :key="i"
        >
          <div class="content">
            <div class="title clearFix">
              <span class="num fl">{{v}}</span>
              <van-icon
                class="icon fr"
                name="delete"
                v-show="orderData[v].status == 2"
                @click="remove(v)"
              />
              <span class="text fr" v-if="orderData[v].status == 2">已收货</span>
              <span v-else class="text fr" @click="receipt(v)">确认收货</span>
            </div>
            <div class="box clearFix" v-for="(val,j) in orderData[v].data" :key="j">
              <div class="img-box fl">
                <img class="auto-img" :src="val.smallImg" alt />
              </div>
              <div class="box-content fl">
                <div class="up clearFix">
                  <span class="name fl">{{val.name}}</span>
                  <span class="price fr">￥{{val.price}}</span>
                </div>
                <div class="center">{{val.enname}}</div>
                <div class="down clearFix">
                  <span class="rule fl">{{val.rule}}</span>
                  <span class="count fr">x{{val.count}}</span>
                </div>
              </div>
            </div>
            <div class="boot clearFix">
              <span class="date fl">{{orderData[v].date}}</span>
              <div class="fr">
                <span>共计 {{orderData[v].count}} 件商品 合计:</span>
                <span class="total">￥{{orderData[v].total}}</span>
              </div>
            </div>
          </div>
        </div>
        <van-empty class="tips" v-show="orderNum.length == 0" description="这里啥都没有鸭" />
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import "../../assets/css/order.less";
export default {
  data() {
    return {
      activeName: "0",
      tabData: [
        {
          title: "全部订单",
          name: 0
        },
        {
          title: "待收货",
          name: 1
        },
        {
          title: "已收货",
          name: 2
        }
      ],
      isReceipt: false,
      orderNum: [],
      orderData: []
    };
  },
  created() {
    this.getOrderData();
  },
  methods: {
    // 切换tab栏显示对应数据
    toggleTab() {
      this.getOrderData();
    },
    // 获取数据所有订单数据
    getOrderData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "get",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString: tkStr,
          status: this.activeName
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 70000) {
            // 把所有订单号存在一起
            let oids = [];
            let orderData = {};
            result.data.result.forEach(val => {
              if (oids.indexOf(val.oid) == -1) {
                oids.push(val.oid);
              }
              if (!orderData[val.oid]) {
                orderData[val.oid] = {
                  status: val.status,
                  data: [val],
                  count: val.count,
                  total: val.count * val.price
                };
                // 切割时间 拼接时间
                let date = val.createdAt.split(".", 1)[0].split("T");
                let hour = date[1].split(":");
                hour[0] = Number(hour[0]) + 8;
                let h = "";
                for (let i = 0; i < hour.length; i++) {
                  h += hour[i] + ":";
                }
                h = h.slice(0, h.length - 1);

                date = date[0] + " " + h;
                orderData[val.oid].date = date;
              } else {
                orderData[val.oid].count += val.count;
                orderData[val.oid].total += val.count * val.price;
                orderData[val.oid].data.push(val);
              }
            });

            
            // 
            // 获取所有不相同的编号
            this.orderNum = oids;
            // 按照编号分类数据
            this.orderData = orderData;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 确认收货
    receipt(oid) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "post",
        url: "/receive",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          oid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 80000) {
            this.orderData[oid].status = 2;
            if (this.activeName == 1) {
              let index = this.orderNum.indexOf(oid);
              this.orderNum.splice(index, 1);
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 删除订单
    remove(oid) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "post",
        url: "/removeOrder",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          oid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 90000) {
            let index = this.orderNum.indexOf(oid);
            this.orderNum.splice(index, 1);
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    }
  }
};
</script>
